import React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import Posts from "./Posts"

const Category = () => {
  const categoryData = useStaticQuery(graphql`
    {
      allDirectory(filter: { relativeDirectory: { regex: "/posts$/" } }, sort: { order: ASC, fields: name }) {
        nodes {
          name
          id
        }
      }
    }
  `)

  const categoryTags = categoryData.allDirectory.nodes.map(category => {
    return (
      <Link className="post-tag" to={`/`} key={category.id}>
        {category.name}
      </Link>
    )
  })

  const categorys = categoryData.allDirectory.nodes.map(category => {
    return (
      <div className={"category_box " + category.name} key={category.id}>
        <div className="category_tit">{category.name}</div>
        <Posts category={category.name} />
      </div>
    )
  })

  return (
    <div>
      <div className="tag_wrap">{categoryTags}</div>
      {categorys}
    </div>
  )
}

export default Category
